import Filter from "../../filter/Filter";

export default function HeaderImage(props) {
  console.log(props.img);
  console.log('test props header', props.setIsOpenA);
  return (
    <div className="header-image" onClick={props.closeInputs}>
      <img src={props.img} alt="" />
        {props.title ? <h1 className="h1">{props.title}</h1> : null}
        {props.title2 ? <div className="h2">{props.title2}</div> : null}
        {props.hasFilter ? <Filter isOpenA={props.isOpenA} setIsOpenA={props.setIsOpenA} mobileTextButton='go to search' /> : null}
      </div>
  );
}
